.entrance-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 90%;
}

.entrance-text {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 40%;
}

.entrance-text h2 {
    font-size: 2.5em;
    padding: 0 5px 0 5px;
}

.entrance-text h1 {
    font-size: 3.5em;
    padding: 0 5px 0 5px;
}

.breef-description {
    text-align: center;
}

.breef-description p {
    font-size: 1.5em;
    font-weight: 350;
    color: rgba(255, 255, 255, 0.9);
}

.js-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.js-container p {
    padding: 0 10px 0 10px;
}

@media screen and (max-width: 700px) {
    .entrance-text {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 40%;
    }

    .entrance-text h2 {
        font-size: 2em;
        padding: 0 5px 0 5px;
    }

    .entrance-text h1 {
        font-size: 2.5em;
        padding: 0 5px 0 5px;
    }
}